
import XHeader from 'vux/src/components/x-header/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'


let echarts = require('echarts/lib/echarts')

require('echarts/lib/chart/bar')

require('echarts/lib/component/tooltip')
require('echarts/lib/component/title')
export default {
name: 'hello',
components: {
XHeader,
XTable,
LoadMore

},
data() {
return {
count: 0
}
},
mounted() {
this.drawLine();
},
methods: {
radios: function() {
var _this = this;
_this.count = 0;
$("input:checked").each(function() {

_this.count = _this.count + Number($(this).val());
console.log(_this.count);

})
},
drawLine() {

let myChart = echarts.init(document.getElementById('myChart'))

myChart.setOption({
color: ['#799241'],
tooltip: {
trigger: 'axis',
axisPointer: { // 坐标轴指示器，坐标轴触发有效
type: 'shadow' 
}
},
grid: {
left: '3%',
right: '4%',
bottom: '3%',
containLabel: true
},
xAxis: [{
type: 'category',
data: ['0-6', '7-13', '14-20', '21-27', '28-35'],
axisTick: {
alignWithLabel: true
}
}],
yAxis: [{
type: 'value',
axisLabel: {
show: true,
interval: 'auto',
formatter: '{value} %'
},
show: true
}],
series: [

{
name: '收入',
type: 'bar',
stack: '总量',
label: {
normal: {
formatter: '{c}%',
show: true,
position: 'top'
}
},
data: [2, 15,38,60,78],

}

]
});
}
}
}



